*,
*::before,
*::after {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
:root{
    --header-page-height:58px;
    --body-page-height:calc( 100vh - 58px);
}
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,700;1,400;1,700&display=swap');

.App {
    display: flex;
    background: url('./asset/image/background.jpg');
    background-position: center;
    background-size: cover;
    min-height: 100vh;
    font-family: 'Poppins', sans-serif;
}
input {
    padding: 4px;
    color: black;
    width: 100%;
    border: 1.3px solid rgb(173, 173, 173);
    outline: none;
    border-radius: 4px;
}
input:focus-visible{
    border-color: #3182ce   ;
    box-shadow: 0 0 0 1px #3182ce;
}

.line-bottom::after{
    content: '';
    position: absolute;
    display: block;
    width: 100%;
    bottom: 0;
    border-bottom: 1px solid #c2c1c1;
}
span {
    display: block;
}

a {
    text-decoration: none;
}

::-webkit-scrollbar {
    display: none;
}